body {
  font: 500 14px "Poppins", sans-serif !important;
}





.dark {
  .owl-carousel .owl-item {
    background-color: theme('colors.dark.pageBg');
  }

  .what_we_do {
    .owl-carousel .owl-item {
      border: 2px solid theme('colors.dark.border');
    }
  }

  .our_service_dtls {
    .owl-carousel .owl-item {
      border: none;
    }
  }

}

.owl-carousel .owl-item {
  background-color: theme('colors.pageBg');
  border-radius: 5px;
  padding: 30px;
}

.what_we_do {
  .owl-carousel {
    .owl-item {
      background-color: transparent !important;
      margin-top: 2rem;
      border-radius: 5px;
      padding: 30px;
      border: 2px solid #e5e7eb;
    }

    .owl-stage {
      display: inline-flex;
    }
  }
}

.our_service_dtls {
  @extend .what_we_do;
  .owl-carousel {
    .owl-stage {
      margin-left: 3px;
    }
    .owl-item {
      padding: 0px;
      border: none;
      display: flex;
    }
    .owl-stage-outer {
      margin: 0 45px;
    }
  }
  .owl-theme .owl-nav {
    margin-top: unset !important; 
    margin: unset !important;
      .owl-next {
        position: absolute;
        top: 30px;
        right: 0;
      }
      .owl-prev {
        position: absolute;
        top: 30px;
        left: 0;
      }

  }
}

.owl-carousel .owl-item img {
  filter: drop-shadow(3px 27px 10px);
}

.owl-prev,
.owl-next {
  width: 35px;
  height: 35px;
  border-radius: 50% !important;
  background: transparent !important;
  border: 1px solid #0ea5e9;
  color: #0ea5e9 !important;
  font-size: 20px !important;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
}